nav{
    display: flex;
    height: 12vh;
    width: 100%;
    background: #1b1b1b;
    align-items: center;
    justify-content: space-between;
    padding: 0 5vh 0 7vh;
    flex-wrap: wrap;
}

nav .logo{
    color: #fff;
    font-size: 5vh;
    font-weight: 600;
}

nav ul{
    display: flex;
    flex-wrap: wrap;
    list-style: none;
}

nav ul li{
    margin: 0 5px;
}

nav ul li a{
    color: #f2f2f2;
    text-decoration: none;
    font-size: 3vh;
    font-weight: 500;
    padding: 2vh 3vh;
    border-radius: 5px;
    letter-spacing: 1px;
    transition: all 0.3s ease;
}

nav ul li a:hover{
    background: #f9004d;
}

nav .menu-btn i{
    color: #fff;
    font-size: 4vh;
    cursor: pointer;
    display: none;
}

input[type="checkbox"]{
    display: none;
  
}

@media (max-width: 1000px){
    nav{
      padding: 0 40px 0 50px;
    }
}

@media (max-width: 920px) {

    nav .menu-btn i{
      display: block;
    }
    #click:checked ~ .menu-btn i:before{
      content: "\f00d";
    }
    nav ul{
      position: fixed;
      top: 12vh;
      left: -100%;
      background: #111;
      height: 100vh;
      width: 100%;
      text-align: center;
      display: block;
      transition: all 0.3s ease;
    }
    #click:checked ~ ul{
      left: 0;
    }
    nav ul li{
      width: 100%;
      margin: 2vh 0;
    }
    nav ul li a{
      width: 100%;
      margin-left: -100%;
      display: block;
      font-size: 20px;
      transition: 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    }
    #click:checked ~ ul li a{
      margin-left: 0px;
    }
    nav ul li a.active,
    nav ul li a:hover{
      background: none;
      color: #f9004d;
    }
}